import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';

// 
import Kuaiji from './kuaiji'
// 
import Jisuanji from './jisuanji'
// 
import Qichedianzi from './qichedianzi'
// 
import Qicheyingxiao from './qicheyingxiao'
// 
import Qicheyunyong from './qicheyunyong'
// 
import Shukong from './shukong'

class Major extends Component {
    render() {
        let { majorMenuUrl } = this.props;
        return (
            <Fragment>
                <Kuaiji url={majorMenuUrl}></Kuaiji>
                <Jisuanji url={majorMenuUrl}></Jisuanji>
                <Qichedianzi url={majorMenuUrl}></Qichedianzi>
                <Qicheyingxiao url={majorMenuUrl}></Qicheyingxiao>
                <Qicheyunyong url={majorMenuUrl}></Qicheyunyong>
                <Shukong url={majorMenuUrl}></Shukong>
            </Fragment>
        )
    }
}

export default connect((state)=> {
    return {
        majorMenuUrl: state.majorMenuReducer.majorMenuUrl
    }
})(Major)

